




























import KeySearchModal from "./KeySearchModal.vue"

export default {
    props: {
        fieldName: String,
        tableName: String,
        rowID: undefined,
        readable: undefined,
        isFilter: {
            type: Boolean,
            default: false
        },
        isNullable: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            ids: [],
            selectedValue: undefined,
            hiddenSelectedValue: undefined,
            showModal: false
        }
    },
    methods: {
        handleUpdate(event) {
            this.selectedValue = event.readable
            this.hiddenSelectedValue = event.id
            this.showModal = false
        }
    },
    components: {
        KeySearchModal
    },
    async mounted() {
        this.selectedValue = this.readable
        this.hiddenSelectedValue = this.rowID
    }
}
