



















import Vue, { PropType } from "vue"
import { Choices } from "../interfaces"

export default Vue.extend({
    props: {
        fieldName: {
            type: String,
            default: ""
        },
        value: {
            type: undefined,
            default: undefined
        },
        choices: {
            type: Object as PropType<Choices>,
            default: () => {
                return {}
            }
        },
        isNullable: {
            type: Boolean,
            default: false
        },
        isFilter: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            localValue: ""
        }
    },
    mounted() {
        this.localValue = this.isFilter ? "all" : this.value
    },
    watch: {
        value(newValue) {
            this.localValue = newValue
        }
    }
})
